document.querySelector('form').addEventListener('submit', function (event) {
    const input_name = event.currentTarget[0].value;
    const input_tel = event.currentTarget[0].value;
    const button = event.currentTarget[2];
    button.classList.add('button--sending');

    setTimeout(function() {
        fetch('/users',
            {
                headers: {
                    'Content-type': 'application/json;charset=UTF-8'
                },
                method: 'POST',
                body: JSON.stringify({text: `${input_name} ${input_tel.replace(/\s/g, '')}`})
            })
            .then(function (response) {
                button.classList.remove('button--sending')
                button.classList.add('button--sent');

                if (!response.ok) {
                    throw Error(response.message);
                }
                return response.json();
            })
            .then(function (json) {
                if (json.code !== '100') {
                    button.innerText = `${json.message} Пожалуйста, свяжитесь с нами самостоятельно`;
                }
                //button.value = `${json.message} Пожалуйста, свяжитесь с нами самостоятельно`;
                console.log('OK ', json);
            })
            .catch(function (error) {
                button.innerText = `${error} Пожалуйста, свяжитесь с нами самостоятельно`;
                console.log('Error: ', error);
            })
    }, 3000)

    event.preventDefault();
});

